.SearchBar {
    margin-top: 1rem;
    margin-bottom: 1rem;
    position: relative;
    box-sizing: border-box;
}

.SearchInput {
    width: 20rem;
    background-color: var(--color-bg);
    color: var(--color-text);
    border-radius: 2.5rem;
    height: 2.5rem;
    border: none;
    float: left;
    padding: 1rem 1.5rem;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: var(--color-text);
  font-size: 1.5rem;
  opacity: 0.7; /* Firefox */
}


.SearchLink {
    margin-top: 0;
    margin-left: -3rem;
    position: absolute;
    top: 60%;
    -ms-transform: translateY(-60%);
    transform: translateY(-60%);
    float: left;
    font-size: 2rem;
    color: black;
}