@import url("https://fonts.googleapis.com/css?family=Montserrat:ital,wght@0,200;0,400;0,600;0,700;1,400;0,900&family=Rubik+Burned&family=Ubuntu+Mono:ital,wght@0,400;0,700;1,400");

:root {
  --color-primary: #7bc99a;
  --color-bg: #d0ecdb;
  --color-secondary: #d6430d;
  --color-text: #363636;
  --max-width: 700px;
  --card-height: 350px;
  font-size: 10px;
}

body {
  margin: 0;
  font-family: Montserrat, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  color: var(--color-text);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
