.Card {
    box-sizing: border-box;
    background: var(--color-bg);
    width: 100%;
    height: var(--card-height);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 30px;
}

.CardInside {
    width: 90%;
    height: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    /*border: 1px solid black;
    border-radius: 20px;*/
}

.CardImg {
    border-radius: 20px;
    width: 100%;
}

.CardLink {
    font-weight: 900;
    font-size: 2rem;
    text-decoration: none;
    color: var(--color-text);
}

.CardLink:hover {
    color: var(--color-primary);
}

.CardDescription {
    font-weight: 500;
    font-size: 1.5rem;
    color: var(--color-text);
}