.Navigation {
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    display: flex;
    gap: 5rem;
    align-items: center;
    justify-content: space-between;
}

.CustomNavLink {
    font-weight: 900;
    font-size: 2rem;
    text-decoration: none;
    color: var(--color-text);
}

.CustomNavLink:hover {
    color: var(--color-primary);
}